export default {
   baseUrl:appConfig.baseUrl,
   homeUrl:appConfig.baseUrl,
   install:function(Vue){
      Vue.prototype.shareOpenApp= (param) => shareOpenApp(param);
      Vue.prototype.androidShareOpenApp= (param) => androidShareOpenApp(param);
      Vue.prototype.getUserPlatforms= () => getUserPlatforms();
      Vue.prototype.ifShowTop=ifShowTop;
   }
}
const ifShowTop=true;//是否显示顶部打开app
 function shareOpenApp(urlString){
   let openAppUrl = appConfig.baseUrl == "https://xwj.juhaolian.cn/" ? appConfig.openAppUrlForiOSProd : appConfig.openAppUrlForiOSTest;
   console.log(openAppUrl)
   if (getUserPlatforms() <= 1) {
      window.location.href = "juhaolian://testxwj.juhaolian.cn/shareToApp-owner-vue/" + urlString
      setTimeout(() => {
         window.location.href= openAppUrl+urlString
       }, 500);
   }else{
      let href = window.location.href;
      if (href.includes("shareGoods/id=") || href.includes("shareArticle/id=") || href.includes("shareSeckill/id=")) {
         window.location.replace(openAppUrl+urlString);
      }else{
         window.location.href= openAppUrl+urlString;
      }
   }
    
}

function androidShareOpenApp(urlString) {
   if (getUserPlatforms() > 1) return
   // console.log("进页面时打开app===》https://testxwjfile.juhaolian.cn/" + urlString)
   window.location.href = "juhaolian://testxwj.juhaolian.cn/shareToApp-owner-vue/" + urlString
}
let getUserPlatforms = function () {

   var ua = window.navigator.userAgent.toLowerCase();
   var u = navigator.userAgent
   console.log(ua);//mozilla/5.0 (iphone; cpu iphone os 9_1 like mac os x) applewebkit/601.1.46 (khtml, like gecko)version/9.0 mobile/13b143 safari/601.1
   if (ua.match(/MicroMessenger/i) == 'micromessenger' && (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) ) {
      return 0;//安卓微信
   }

   /**判断是在Android终端内*/
   const isInAndroid = () => {
      return u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
   }
   /**判断是在iOS终端内*/

   const isInIOS = () => {
      return !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
   }

   const isMobile = () => {
      return !!u.match(/AppleWebKit.*Mobile.*/) //是否为移动终端
   }
   if (isInAndroid()) {
      return 1    //android机器的话  就是返回1
   } else if (isInIOS()) {
      return 2    //ios 设备返回2
   } else {
      return 3
   }
}
