<template>
  <div
    style="display:flex;flex-direction:column;width:100%;position:relative;flex:1;position: absolute;height: 100%;overflow:hidden;"
  >
    <div id="goodsPage" ref="scrollView" class="wrapper">
      <div>
        <!--商品轮播图-->
        <div style="position:relative;">
          <van-swipe class="swipe" :loop="true">
            <van-swipe-item v-for="image in lists.spuPicUrlList" :key="image">
              <img :src="image" />
            </van-swipe-item>
          </van-swipe>
          <div class="outSellBox" v-if="skuList.storeNumber==0">补货中</div>
        </div>

        <!--商品价格和库存-->
        <div class="price" ref="changeTop">
          <span class="priceTag">￥</span>
          <span class="salePrice">{{skuList.skuPrice}}</span>
          <span class="originalPrice" v-if="lists.priceLine!=''">￥{{lists.priceLine}}</span>
          <span
            class="stock"
            v-show="skuList.storeNumber!=0&&skuList.storeNumber>lists.threshold"
          >库存{{skuList.storeNumber}}件</span>
          <span
            class="lowStock"
            v-show="skuList.storeNumber!=0&&skuList.storeNumber<=lists.threshold"
          >仅剩{{skuList.storeNumber}}件</span>
        </div>
        <!--商品名称\简介和分享-->
        <div class="goods">
          <div class="goodsDetailBox">
            <div>
              <div class="spuName">{{lists.spuName}}</div>
              <div class="salePoint">{{lists.spuSalePoint}}</div>
            </div>
          </div>
          <div class="goodsAttribute">
            <div
              class="goodsAttributeBox"
              :style="{'border':'0.5px solid #FB5768'}"
              v-if="lists.goodsBrand"
            >品牌：&nbsp;{{lists.goodsBrand}}</div>
            <div
              class="goodsAttributeBox"
              :style="{'border':'0.5px solid #FB5768'}"
              v-if="lists.prodArea"
            >产地：&nbsp;{{lists.prodArea}}</div>
          </div>
        </div>

        <!--商品标签.规格.领券.购买数量-->
        <div class="tag" ref="productTag">
          <div class="oneTag" v-show="lists.cutOffTimeStr">
            <div class="redPoint"></div>
            <div class="tagInfo">{{lists.cutOffTimeStr}}</div>
          </div>
          <div
            class="oneTag"
            v-show="lists.labelList.length!=0"
            v-for="(tagInfoItem,index) in lists.labelList"
            :key="index"
          >
            <div class="redPoint"></div>
            <div class="tagInfo">{{tagInfoItem}}</div>
          </div>
        </div>
        <div class="spec">
          <div class="specTit">规格</div>
          <div class="specName">{{skuList.attrValueContent}}</div>
        </div>

        <div class="borderLine"></div>

        <!--购买数量-->
        <div class="shoppingNum">
          <div>购买数量</div>
          <van-stepper class="stepper" v-model="goodsNum" integer></van-stepper>
        </div>
        <div class="borderBox"></div>

        <!-- 店铺信息 -->
        <div class="shopInfoBox" v-if="Object.keys(shopInfo).length != 0">
          <div class="shopInfo">
            <div>
              <img class="shopPic" :src="shopInfo.shopPicUrl==null?ownerPicUrl:shopInfo.shopPicUrl" />
            </div>
            <div>
              <div class="shopName">{{shopInfo.shopName}}</div>
              <div class="goodsSaleNum">在售商品{{shopInfo.saleCount}}件</div>
            </div>
          </div>
          <div class="toShop">
            进店逛逛 &nbsp;
            <img :src="icon.evaluate" />
          </div>
        </div>
        <div class="borderBox"></div>

        <!--商品评价-->
        <div class="evaluate" ref="titChange">
          <div class="evaluateTit">商品评价</div>
          <div class="evaluateCli" @click="showMoreEval()">
            <span>查看所有评价</span>
            <img :src="icon.evaluate" />
          </div>
          <!--<span>{{evalContent}}</span>-->
        </div>
        <!--显示第一条评价-->
        <div v-if="show.firstEvaluate" class="evaContent">
          <div
            :style="{'display': 'flex','align-items': 'center','justify-content': 'space-between'}"
          >
            <div :style="{'display': 'flex','align-items': 'center'}">
              <img
                class="evalOwnerPic"
                :src="(evalInfo.ownerPicUrl==null || evalInfo.ownerPicUrl=='')?ownerPicUrl:evalInfo.ownerPicUrl"
              />
              <!--如果用户头像为空则显示默认头像-->
              <div class="ownerName">{{evalInfo.ownerNickname}}</div>
            </div>
            <van-rate
              class="evaluateRate"
              v-model="evalInfo.evalLevel"
              size="0.37rem"
              allow-half
              gutter="3"
              :icon="icon.like"
              :void-icon="icon.noLike"
              void-color="#eee"
              readonly
            />
          </div>
          <div class="evaTxt">{{evalInfo.evalContent}}</div>
          <div style="display:flex;">
            <div v-for="(item, index) in evalInfo.evalPicUrl" :key="index">
              <img class="evalPic" :src="item.picUrl" />
            </div>
          </div>
        </div>
        <!--商品详情-->
        <!-- <div class="describe" ref="detailChange">
      <div class="describeTxt">-商品详情-</div>
      <div class="describeDetail" v-html="lists.detailDescribe"></div>
        </div>-->
        <!--商品详情-->
        <div
          class="noDescribe"
          v-if="!lists.detailDescribe"
          ref="detailChange"
          style="margin-bottom:20px;"
        ></div>
        <div class="describe" v-if="lists.detailDescribe" ref="detailChange">
          <div class="describeTxt">-商品详情-</div>
          <div class="describeDetail" v-html="lists.detailDescribe"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
import utils from "../../store/utils";
import { getProductInfo, getProductShopInfo } from "../../service/shareService";
export default {
  name: "goodsDetail",
//   components: {
//     PageHeader
//   },
  data() {
    return {
      lists: [],
      skuList: [],
      skuNum: [], //购物车角标
      collectCode: [],
      cutOffTimeStr: [],
      icon: {
        arrowBg: require("../../assets/goodsDetail/navbar_icon_arrow_blackBg@2x.png"),
        arrow: require("../../assets/goodsDetail/navbar_icon_arrow_black@2x.png"),
        topClick: require("../../assets/goodsDetail/sub_navbar_icon_choicePoint@2x.png"),
        evaluate: require("../../assets/goodsDetail/mall_icon_more.png"),
        like: require("../../assets/goodsDetail/detail_commentList_icon_start_click.png"),
        noLike: require("../../assets/goodsDetail/detail_commentList_icon_start_normal.png"),
        xwjlogo: require("../../assets/share/shareLogo.png"),
        shareGuide: require("../../assets/share/share_guide_img.png")
      },
      show: {
        weChartShare: false, //微信分享
        firstEvaluate: true, //是否有第一条评价
        moreDetail: false //查看更多弹窗
      },
      evalInfo: [],
      num: 1, //购买数量
      detailDescribe: [],
      ownerPicUrl: require("../../assets/mine/user_Default_avata.png"),
      spuId: "",
      goodsNum: 0,
      shopInfo: {}
    };
  },
  methods: {
    onCancel() {
      //取消微信分享
      this.show.moreDetail = false;
    },

    getGoodsDetail() {
      const _this = this;
      const data = {
        spuCode: window.location.href.split('=')[1]
      };
      getProductInfo(data).then(function(response) {
        if (response.resultCode == 0) {
          console.log(response.data.shareData);
          _this.lists = response.data;
          if (
            _this.lists.cutOffTimeStr &&
            _this.lists.cutOffTimeStr.indexOf("<br/>")
          ) {
            _this.lists.cutOffTimeStr = _this.lists.cutOffTimeStr.replace(
              /<br\/>/g,
              "\n"
            );
            console.log(_this.lists.cutOffTimeStr);
          }
          document.title = _this.lists.spuName;
          _this.skuList = response.data.skuList[0];
          _this.evalInfo = response.data.evalInfo;
          _this.skuNo = response.data.skuList[0].skuNo;
          _this.shopCode = response.data.shopCode;
          _this.spuCode = response.data.spuCode;
          let urlString = "product?spuCode=" + _this.lists.spuCode;
          _this.getShopInfo(_this.shopCode);
          const eva = response.data.evalInfo;
          if (eva == null) {
            _this.show.firstEvaluate = false;
          } else {
            console.log("有第一条评价");
            _this.show.firstEvaluate = true;
          }
        } else {
          Dialog.alert({
            message: response.msg
          }).then(() => {});
        }
      });
    },
    // 获取店铺简略信息
    getShopInfo(shopCode) {
      console.log("店铺详情==========>");
      const data = {
        shopCode: shopCode
      };
      getProductShopInfo(data).then(response => {
        this.shopInfo = response.data;
      });
    },
    //查看商品参数弹窗
    moreDetail() {
      this.show.moreDetail = true;
    }
  },
  mounted: function() {
    this.getGoodsDetail();
    document.addEventListener(
      "touchmove",
      function(evt) {
        evt.preventDefault();
      },
      { passive: false }
    );
    var height =
      window.innerHeight ||
      document.documentElement.clientHeight ||
      document.body.clientHeight;
  }
};
</script>
