<template>
  <div class="read-text-page">
    <page-header @btn-action="openApp" />
    <div class="read-content" v-html="detailHtml"></div>
  </div>
</template>

<script>
import { getProtocolService } from "../../service/shareService";
import PageHeader from "../../components/WechatShare/PageHeader";
import { Toast } from "vant";
export default {
  components: {
    PageHeader
  },
  name: "readTextPage",
  mounted() {
    // 获取传参
    let routerParams = this.$route.query;
    if (routerParams != undefined && routerParams.pageType != undefined) {
      this.pageType = routerParams.pageType;
    }
    // 获取页面数据
    this.getReadText();
  },
  data() {
    return {
      pageType: 1,
      title: "信我家服务协议",
      detailHtml: ""
    };
  },

  methods: {
    openApp() {
      let urlString = "";
      this.shareOpenApp(urlString);
    },
    // 过滤style标签内容
    filterHtml: function(html) {
      var rel = /style\s*?=\s*?([‘"])[\s\S]*?\1/;
      var newHtml = "";
      if (html) {
        newHtml = html.replace(new RegExp(rel, "g"), "");
      }
      return newHtml;
    },
    onBack() {
      this.$router.back(-1);
    },
    // 加载关于信息
    getReadText() {
      let reqData = {
        PROTOCOL_CODE: "1"
      };
      if (1 == this.pageType) {
        //查看海信服务协议
        reqData.PROTOCOL_CODE = "1";
        this.title = "信我家服务协议";
      } else if (3 == this.pageType) {
        //查看隐私保护协议
        reqData.PROTOCOL_CODE = "3";
        this.title = "隐私保护协议";
      } else if (5 == this.pageType) {
        //查看隐私保护协议
        reqData.PROTOCOL_CODE = "5";
        this.title = "积分说明";
      }
      getProtocolService(reqData)
        .then(noteData => {
          let htmlStr = noteData["CONTENT"];
          this.detailHtml = htmlStr;
          // this.detailHtml = this.filterHtml(htmlStr);
        })
        .catch(error => {
          Toast("网络异常,请稍后再试");
        });
    }
  }
};
</script>

