<template>
  <div class="pageView">
    <page-header @btn-action="openApp" />
    <div
      v-if="Object.keys(shareData).length !=0"
      class="liveView"
      :style="{'background':'url('+shareData.imgUrl +')'+ 'no-repeat' + ' ' + 'center' }"
    >
      <div class="overLay"></div>
      <div class="absoView">
        <div class="topLogo" v-if="shareData.isAlive">
          <img src="../../assets/share/shareLogo.png" />
        </div>
        <div class="endInfo" v-else>
          <span class="endText">直播已结束</span>
          <span class="totalTime">直播时长：{{formatTime(shareData.duration)}}</span>
        </div>
        <div
          class="midContent"
          :style="{'background':'url('+shareData.imgUrl +')'+ 'no-repeat' + ' ' + 'center' }"
        >
          <div class="liveInfo">
            <span class="liveTitle">{{shareData.liveTitle}}</span>
            <div class="showInfo">
              <div class="showStatus">
                <div class="cyclePoint"></div>
                <span>{{shareData.isAlive ? "直播中" : "已结束"}}</span>
              </div>
              <span>{{shareData.viewersNum + (shareData.isAlive ? '人正在观看' : "人观看")}}</span>
            </div>
          </div>
        </div>
        <div class="bottomBtn" @click="openApp">打开APP</div>
      </div>
    </div>
  </div>
</template>



<script>
import PageHeader from "../../components/WechatShare/PageHeader";
import { getLiveDetail } from "../../service/shareService";
export default {
  components: {
    PageHeader
  },
  data() {
    return {
      livePic:
        "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1590643334846&di=581439c3bb25705f4ac5c891c2b13207&imgtype=0&src=http%3A%2F%2Fa2.att.hudong.com%2F36%2F48%2F19300001357258133412489354717.jpg",
      shareData: {},
      liveId: "",
      roomId: ""
    };
  },
  mounted() {
    this.liveId = this.$route.params.id.split("=")[1];
    this.initLiveData();
  },
  methods: {
    openApp() {
      let urlString =
        "myfamily?type=3&liveId=" + (this.roomId ? this.roomId : "");
      this.shareOpenApp(urlString);
    },
    initLiveData() {
      let param = { id: this.liveId };
      getLiveDetail(param)
        .then(res => {
          if (res.resultCode == 0) {
            this.shareData = res.data.shareData;
            this.roomId = res.data.shareData.roomId;
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(error => {
          this.$toast("网络异常,请稍后再试");
        });
    },
    formatTime(time) {
      var hour = parseInt(time / 3600);
      var minute = parseInt((time % 3600) / 60);
      var miao = parseInt(time % 60);
      var hh = (hour < 10 ? "0" + hour : hour) + ":";
      var mm = (minute < 10 ? "0" + minute : minute) + ":";
      var ss = miao < 10 ? "0" + miao : miao;
      return hh + mm + ss;
    }
  }
};
</script>

