<script>
import { Toast } from "vant";
import PageHeader from "../../components/WechatShare/PageHeader";
import Bscroll from "@better-scroll/core";
import {
  getAdvertShopsDetail,
  getShareActivityDetail,
} from "../../service/shareService";
export default {
  name: "shopsActivity",
  components: {
    PageHeader,
  },
  data() {
    return {
      shareId: "",
      activityId: "",
      list: [],
      topImgUrl: "",
      useAndroidCustom: true,
      useCustom: false,
      showEmptyView: false,
    };
  },
  mounted() {
    this.shareId = this.$route.params.id.split("=")[1];
    this.initData();
    document.addEventListener(
      "touchmove",
      function (evt) {
        evt.preventDefault();
      },
      { passive: false }
    );
    this.$nextTick(() => {
      let wrapper = document.querySelector(".wrapper");
      let scroll = new Bscroll(wrapper, { click: true });
      setInterval(() => {
        scroll.refresh();
        console.log("scroll刷新");
      }, 2000);
    });
  },
  methods: {
    initData() {
      let param = { id: this.shareId };
      getAdvertShopsDetail(param)
        .then((res) => {
          if (res.resultCode == 0) {
            this.list = res.data.shareData.shopList;
            this.topImgUrl = res.data.shareData.bannerUrl;
            this.activityId = res.data.advertId;
            this.getActivityTepmlate();
            if (!res.data.shareData) {
              this.showEmptyView = true;
            } else {
              this.showEmptyView = false;
            }
          } else {
            this.showEmptyView = true;
          }
        })
        .catch((error) => {
          this.showEmptyView = true;
          Toast("网络请求失败,请稍后再试");
        });
    },
    //获取活动模板信息
    getActivityTepmlate() {
      const data = {
        id: this.activityId,
      };
      getShareActivityDetail(data).then((res) => {
        if (res.resultCode == 0 && res.data) {
          // this.showFinishView = res.data ? res.data.dataStatus != "1" : true;
          document.title = res.data.title;
        }
      });
    },
    goToShop() {
      this.openApp();
    },
    openApp() {
      let urlString = "myfamily";
      if (this.activityId) {
        urlString = "myfamily?type=9&activityId=" + this.activityId;
      }
      this.shareOpenApp(urlString);
    },
  },
};
</script>

