import axios from 'axios'
import config from './common/config'
import 'lib-flexible'
import  comSearchBar from './components/common/comSearchBar.vue'
import newStepper from './components/common/stepper.vue'
import topTitle from '@/components/goodsDetail/topTitle.vue';
import wechartShare from '@/components/goodsDetail/wechartShare.vue'
