import Vant from 'vant';
import 'vant/lib/index.css';
//RSA加密插件
import JSEncrypt from 'jsencrypt';
Vue.use(Vant);
Vue.use(Toast);
Vue.use(Lazyload)

Vue.use(config);
Vue.use(MetaInfo);
//Vue.ues(newStepper);
Vue.use(comSearchBar);
Vue.use(Dialog);

Vue.prototype.$http = axios;
Vue.config.productionTip = false;
Vue.prototype.$jsencrypt = JSEncrypt;
// //jsrsasign获取签名
Vue.prototype.$getRSASign = function (password, key) {
  let sign = new JSEncrypt({
    default_key_size: 1024
  });
  key = key.replace("/r", "");
  console.log(key);
  key = "-----BEGIN PRIVATE KEY-----" + key + "-----END PRIVATE KEY-----"
  sign.setPublicKey(key);
  let data = sign.encrypt(password.toString());
  return data;
};

new Vue({
    router,
    store,
  topTitle,
  wechartShare,
  newStepper,
  [Dialog.Component.name]: Dialog.Component,
  render: h => h(App)
}).$mount('#app')

