<template>
  <div class="notice-detail-page" :class="{themeL:bindThemeL}">
    <page-header @btn-action="openApp" />
    <div class="notice-content" v-if="noticeDetail != null">
      <div class="notice-title">{{noticeDetail.noticeName}}</div>
      <div class="notice-time">{{noticeDetail.noticeTime}}</div>
      <div class="contentArea" v-html="noticeDetail.content"></div>
      <div class="bottomBtn" @click="openApp">打开APP</div>
    </div>
  </div>
</template>

<script>
import { getNoticeDetail } from "../../service/shareService";
import PageHeader from "../../components/WechatShare/PageHeader";
import { Toast } from "vant";

export default {
  name: "noticeDetail",
  components: {
    PageHeader
  },
  data() {
    return {
      active: 0,
      title: "物业通知",
      //通知内容
      noticeDetail: null,
      noticeId: ""
    };
  },
  mounted() {
    this.noticeId = this.$route.params.id.split("=")[1];
    this.loadNoticeDetail();
  },
  methods: {
    // 加载通知详情
    loadNoticeDetail() {
      getNoticeDetail({ id: this.noticeId })
        .then(res => {
          if (res.resultCode == 0) {
            this.noticeDetail = res.data.shareData;
          } else {
            Toast(res.msg);
          }
        })
        .catch(error => {
          Toast("网络异常，请稍后重试！");
        });
    },
    openApp() {
      let urlString = "myfamily?type=4";
      this.shareOpenApp(urlString);
    }
  }
};
</script>

