            <!-- 根据接口判断秒杀状态，在轮播图上显示圆形蒙版-->
            <div class="outSellBox" v-if="status==0">即将开始</div>
            <div class="outSellBox" v-if="status==3||status==4">补货中</div>
          </van-swipe-item>
          <div class="van-swipe__indicators" v-if="productImgs.length==1">
            <i class="van-swipe__indicator van-swipe__indicator--active"></i>
