        <!-- 即将开始的秒杀和秒杀中（包括已售罄） -->
        <!-- 即将开始的秒杀和秒杀中（包括补货中） -->
    <div
      class="price"
      v-if="status!=2&&status!=-1"
      :style="{'background':(status==0?'#F5A623':(status==1?'#FB5768':'#999999'))}"
