      <div class="priceLeftBox">
        <div class="priceTag">￥</div>
        <div class="salePrice">{{flashSaleData.spuSkPrice}}</div>
        <div class="originalPrice" v-if="flashSaleData.originalPrice !=null">￥{{flashSaleData.originalPrice}}</div>
      </div>
      <div class="priceRightBox">
        <div class="repertoryBox">
          <div>秒杀库存{{flashSaleData.skStoreBal}}件</div>
          <div :style="{'color':(status==0?'#F5A623':(status==1?'#FB5768':'#646464'))}">限购{{flashSaleData.limitAccount}}件</div>
        </div>
        <div class="timeRightBox">
          <div class="seckillTxt" v-if="status==0" style="color: #F5A623">距离开始仅剩</div>
          <div class="seckillTxt" v-if="status==1||status==3||status==4">距离结束仅剩</div>
          <van-count-down :time="flashSaleData.time" @finish="countDownFinish">
            <template v-slot="timeData" class="seckillDate">
          <span
                  class="item"
                  :style="{'background':(status==0?'#F5A623':(status==1?'#FB5768':'#646464'))}"
          >{{ timeData.hours}}</span>
              <span :style="{'color':(status==0?'#F5A623':(status==1?'#FB5768':'#646464'))}">:</span>
              <span
                      class="item"
                      :style="{'background':(status==0?'#F5A623':(status==1?'#FB5768':'#646464'))}"
              >{{ timeData.minutes}}</span>
              <span :style="{'color':(status==0?'#F5A623':(status==1?'#FB5768':'#646464'))}">:</span>
              <span
                      class="item"
                      :style="{'background':(status==0?'#F5A623':(status==1?'#FB5768':'#646464'))}"
              >{{ timeData.seconds}}</span>
            </template>
          </van-count-down>
          <!-- <detailCountDown v-if="status==0" :timeBox="flashSaleData.time">

          </detailCountDown> -->
        </div>
      </div>

    </div>
    <!-- 已结束的秒杀商品 -->
    <div class="finishedPrice" :style="{'background':'#999999'}" v-if="status==2">
      <div>
        <div class="priceTag">￥</div>
        <div class="salePrice">{{flashSaleData.spuSkPrice}}</div>
        <div class="originalPrice" v-if="flashSaleData.originalPrice">￥{{flashSaleData.originalPrice}}</div>
      </div>
      <div class="seckillTxt">秒杀已结束</div>
    </div>
        <!--商品名称\简介和分享-->
        <div class="goods">
          <div>
            <div class="spuName">{{lists.spuName}}</div>
            <div class="salePoint">{{lists.spuSalePoint}}</div>
            <div class="goodsAttribute">
              <div
                class="goodsAttributeBox"
                :style="{'border':'0.5px solid #FB5768','max-width':'160px'}"
                v-if="lists.goodsBrand"
              >品牌：&nbsp;{{lists.goodsBrand}}</div>
              <div
                class="goodsAttributeBox"
                :style="{'border':'0.5px solid #FB5768'}"
                v-if="lists.prodArea"
              >产地：&nbsp;{{lists.prodArea}}</div>
            </div>
          </div>
        </div>

        <!--商品标签.规格.领券.购买数量-->
        <div class="tag" ref="productTag">
          <div class="oneTag" v-show="lists.cutOffTimeStr">
            <div class="redPoint"></div>
            <div class="tagInfo">{{lists.cutOffTimeStr}}</div>
          </div>
          <div class="oneTag" v-show="lists.labelList.length!=0" v-for="(tagInfoItem,index) in lists.labelList" :key="index">
            <div class="redPoint"></div>
            <div class="tagInfo">{{tagInfoItem}}</div>
          </div>
        </div>

        <div class="spec">
          <div class="specTit">规格</div>
          <div class="specName">{{skuList.attrValueContent}}</div>
        </div>
        <div class="borderLine"></div>
        <!--购买数量-->
        <div class="shoppingNum">
          <div>购买数量</div>
          <van-stepper class="stepper" v-model="goodsNum" integer></van-stepper>
        </div>
        <div class="borderBox"></div>
        <!-- 店铺信息 -->
        <div class="shopInfoBox" @click="openApp" v-if="Object.keys(shopInfo).length != 0">
          <div class="shopInfo">
            <div>
              <img
                class="shopPic"
                :src="shopInfo.shopPicUrl==null?icon.ownerPicUrl:shopInfo.shopPicUrl"
              />
            </div>
            <div>
              <div class="shopName">{{shopInfo.shopName}}</div>
              <div class="goodsSaleNum">在售商品{{shopInfo.saleCount}}件</div>
            </div>
          </div>
          <div class="toShop">
            进店逛逛 &nbsp;
            <img :src="icon.evaluate" />
          </div>
        </div>
        <div class="borderBox"></div>

        <!--商品评价-->
        <div class="evaluate" ref="titChange">
          <div class="evaluateTit">商品评价</div>
          <div class="evaluateCli" @click="openApp()">
            <span>查看所有评价</span>
            <img :src="icon.evaluate" />
          </div>
        </div>
        <!--显示第一条评价-->
        <div v-if="show.firstEvaluate" class="evaContent">
          <div
            :style="{'display': 'flex','align-items': 'center','justify-content': 'space-between'}"
          >
            <div :style="{'display': 'flex','align-items': 'center'}">
              <img
                class="evalOwnerPic"
                :src="(evalInfo.ownerPicUrl==null || evalInfo.ownerPicUrl=='')?icon.ownerPicUrl:evalInfo.ownerPicUrl"
              />
              <!--如果用户头像为空则显示默认头像-->
              <div class="ownerName">{{evalInfo.ownerNickname}}</div>
            </div>
            <van-rate
              class="evaluateRate"
              v-model="evalInfo.evalLevel"
              size="0.37rem"
              allow-half
              gutter="3"
              :icon="icon.like"
              :void-icon="icon.noLike"
              void-color="#eee"
              readonly
            />
          </div>
          <div class="evaTxt">{{evalInfo.evalContent}}</div>
          <div v-for="(item, index) in evalInfo.evalPicUrl" :key="index">
            <img class="evalPic" v-lazy="item.picUrl" />
          </div>
        </div>
        <!--商品详情-->
        <!-- <div class="describe" ref="detailChange">
      <div class="describeTxt">-商品详情-</div>
      <div class="describeDetail" v-html="lists.detailDescribe"></div>
        </div>-->
        <!--商品详情-->
        <div
          class="noDescribe"
          v-if="!lists.detailDescribe"
          ref="detailChange"
          style="margin-bottom:60px;"
        ></div>
        <div class="describe" v-if="lists.detailDescribe" ref="detailChange">
          <div class="describeTxt">-商品详情-</div>
          <div class="describeDetail" v-html="lists.detailDescribe"></div>
        </div>
      </div>

      <!-- 放大预览图片 -->
      <!-- <Preview :images="images" ref="myPreview" ></Preview> -->
    </div>
    <van-goods-action v-show="show.networkError==false">
      <van-goods-action-icon :icon="icon.shop" text="商家" @click="openApp()" />
      <van-goods-action-icon :icon="icon.callShop" text="联系商家" @click="openApp()" />

      <van-goods-action-button
        v-show="status==0&&flashSaleData.time>300000"
        :style="{'background': '#F5A623'}"
        type="danger"
        :text="remind"
        @click="openApp()"
      />
      <!-- 距离开抢5分钟时显示 -->
      <van-goods-action-button
        v-if="status==0&&flashSaleData.time<=300000"
        :style="{'opacity': '0.53','background': '#F5A623'}"
        type="danger"
        text="即将开始"
        @click="openApp()"
      />
      <!-- 秒杀中 -->
      <van-goods-action-button
        v-if="status==1"
        :style="{'background-image':'linear-gradient(135deg, #FFC349 0%, #FC5F6F 66%, #FB5768 100%)'}"
        type="danger"
        text="立即秒杀"
        @click="openApp()"
      />
      <!-- 已售罄 -->
      <van-goods-action-button
        v-if="status==3||status==4"
        :style="{'background': '#999999'}"
        type="danger"
        text="补货中"
        @click="openApp()"
      />
      <!-- 秒杀结束已下架 -->
      <van-goods-action-button
        v-if="status==2"
        :style="{'background': '#999999'}"
        type="danger"
        text="已结束"
        @click="openApp()"
      />
    </van-goods-action>
  </div>
</template>

<script>
import { Dialog } from "vant";
import {
  getShareFlashSaleGoods,
  getProductInfo,
  getProductShopInfo
} from "../../service/shareService";
import { mapActions, mapState } from "vuex";
import detailCountDown from "../../components/flashSaleCountDown/goodsCountDown";
import { ImagePreview } from "vant";
import PageHeader from "../../components/WechatShare/PageHeader"
import Bscroll from "@better-scroll/core"
import { Toast } from "vant";
export default {
  name: "flashSaleGoods",
  data() {
    return {
      lists: {
        spuPicUrlList: []
      },
      activitySpuId: "",
      flashSaleData: {}, //商品秒杀信息
      shopInfo: {}, //店铺信息
      skuList: [],
      goodsNum: 0, //购买数量
      icon: {
        // share: require("@/assets/images/mall/mall_icon_share_gray.png"),
        callShop: require("../../assets/common/mall_icon_call.png"),
        shop: require("../../assets/common/mall_icon_shop.png"),
        evaluate: require("../../assets/goodsDetail/mall_icon_more.png"),
        like: require("../../assets/goodsDetail/detail_commentList_icon_start_click.png"),
        noLike: require("../../assets/goodsDetail/detail_commentList_icon_start_normal.png"),
        xwjlogo: require("../../assets/share/shareLogo.png"),
        noNetworkImg: require("../../assets/common/no_network.png"),
        ownerPicUrl: require("../../assets/mine/user_Default_avata.png")
      },
      show: {
        normalTitle: true, //透明吸顶
        activeTitle: false, //不透明吸顶
        titShow: 1, //顶部标题的详情和评价点击切换
        weChartShare: false, //微信分享
        callShop: false,
        firstEvaluate: true,
        showDialog: false, //显示遮罩层
        goodsDown: false, //已下架弹窗
        saleOut: false, //已售罄弹窗
        remindSale: false, //订阅弹窗
        bannerChangeBool: false,
        networkError: false //网络异常请求
      },
      evalInfo: {},
      time: 1000, //倒计时
      status: -1, //商品状态 0:未开始 1:进行中 2:已结束 3:已暂停(app显示售罄) 4:已售罄 ,
      remind: "开抢时提醒我",
      isShowTopBtn: false, //是否显示置顶按钮
      images: [], // 图片预览属性
      productImgs: []
    };
  },
  components: {
    // "Preview": Preview,
    detailCountDown,
    PageHeader
  },
  methods: {
    //置顶按钮
    goTopBtn() {
      this.$refs.goodsPageScroll.scrollTop = 0;
    },
    bannerChange() {
      this.show.bannerChangeBool = true;
      setTimeout(() => {
        this.show.bannerChangeBool = false;
      }, 500);
    },
    //商品图片缓存完后替换商品图，优化显示效果
    productImgDownload() {
      let picNum = 0;
      let that = this;
      this.lists.spuPicUrlList.map((item, i) => {
        var img = new Image();
        img.onload = function() {
          picNum += 1;
          console.log("商品详情图片" + picNum);
          console.log(that.lists.spuPicUrlList.length + "===========" + picNum);
          if (that.lists.spuPicUrlList.length == picNum) {
            console.log("===========" + picNum);
            that.productImgs = that.lists.spuPicUrlList;
          }
        };
        img.src = that.lists.spuPicUrlList[i];
      });
    },
    //查看商品大图
    bannerPreview(bannerImgArr, index) {
      if (this.show.bannerChangeBool) {
        return;
      }
      this.images = bannerImgArr;
      this.$refs.myPreview.setIndex(index);
      this.$refs.myPreview.setIsShowPreview(true);
      this.$store.dispatch("setPreviewId", this.$refs.myPreview);
    },
    preview(evalPicUrl, index) {
      let srcArr = [];
      evalPicUrl.map(evalPicUrlItem => {
        srcArr.push(evalPicUrlItem.picUrl);
      });
      let that = this;
      let previewId = ImagePreview({
        images: srcArr,
        startPosition: index,
        onClose() {
          that.$store.dispatch("setPreviewId", "");
        }
      });
      this.$store.dispatch("setPreviewId", previewId);
    },
    //获取秒杀商品信息
    init(spuCode) {
      const data = {
        spuCode: spuCode
      };
      getProductInfo(data)
        .then(response => {
          if (response.resultCode == "0") {
            this.lists = response.data;
            if(this.lists.cutOffTimeStr&&this.lists.cutOffTimeStr.indexOf("<br/>")){
              this.lists.cutOffTimeStr= this.lists.cutOffTimeStr.replace(/<br\/>/g,'\n')
            }
            this.productImgDownload();
            this.skuList = response.data.skuList[0];
            this.evalInfo = response.data.evalInfo;
            this.getShopInfo(this.lists.shopCode);
            const eva = response.data.evalInfo;
            if (eva == null) {
              this.show.firstEvaluate = false;
            } else {
              console.log("有第一条评价");
              this.show.firstEvaluate = true;
            }
          } else {
            Dialog.alert({
              message: response.msg
            }).then(() => {});
          }
        })
        .catch(error => {
          // this.show.networkError=true;
        });
    },
    //通过activityId获取商品秒杀信息
    flashSaleDetail() {
      const data = {
        id: this.activitySpuId
      };
      getShareFlashSaleGoods(data)
        .then(response => {
          this.status = response.data.shareData.spuStatus;
          this.flashSaleData = response.data.shareData;
          let urlString ="flashSaleGoods?spuCode=" + this.flashSaleData.spuCode + "&activitySpuId=" + this.flashSaleData.activitySpuId;
          this.androidShareOpenApp(urlString); //android 拉起APP
          this.init(this.flashSaleData.spuCode);
          if (this.status == 0) {
            this.flashSaleData.time =
              (this.flashSaleData.startTime - this.flashSaleData.nowTime) *
              1000;
            console.log("获取到的时间" + this.flashSaleData.time);
            this.remind =
              this.flashSaleData.isSubsribe == 0 ? "开抢时提醒我" : "取消订阅";
          } else {
            this.flashSaleData.time =
              (this.flashSaleData.endTime - this.flashSaleData.nowTime) * 1000;
            console.log("获取到倒计时的时间" + this.flashSaleData.time);
          }
        })
        .catch(error => {
          Toast("网络异常，请稍后重试！");
        });
    },
    // 获取店铺简略信息
    getShopInfo(shopCode) {
      console.log("店铺详情==========>");
      const data = {
        shopCode: shopCode
      };
      getProductShopInfo(data).then(response => {
        this.shopInfo = response.data;
      });
    },
    openApp() {
      let urlString =
        "flashSaleGoods?spuCode=" +
        this.flashSaleData.spuCode +
        "&activitySpuId=" +
        this.flashSaleData.activitySpuId;
      if (!this.flashSaleData.spuCode || !this.flashSaleData.activitySpuId) {
        urlString = "";
      }
      this.shareOpenApp(urlString);
    },
    handleScroll: function() {
      //监听滚动设置吸顶格式
      const _this = this;
      _this.$nextTick(() => {
        let scrollTop = _this.$refs.goodsPageScroll.scrollTop;
        const changeTodetail = _this.$refs.detailChange.offsetTop;
        if (scrollTop > 70) {
          _this.show.activeTitle = true;
          _this.show.normalTitle = false;
          if (scrollTop >= changeTodetail) {
            //吸顶详情和评价的切换
            _this.show.titShow = 2;
          } else {
            _this.show.titShow = 1;
          }
          _this.isShowTopBtn = true;
        } else {
          _this.show.activeTitle = false;
          _this.show.normalTitle = true;
          _this.isShowTopBtn = false;
        }
      });
    },
    //倒计时结束时执行
    countDownFinish() {
      this.flashSaleDetail();
    }
  },
  // updated () {
  //   this.$nextTick(()=>{
  //     let wrapper = document.querySelector('.wrapper') 
  //     let scroll = new Bscroll(wrapper, {})
  //   })
  // },
  mounted: function() {
    this.activitySpuId = this.$route.params.id.split("=")[1];
    this.flashSaleDetail();
    // console.log(this.$userInfo.OWNER_CODE+"dfsdfsd"+this.$userInfo.AREA_CODE)
    document.addEventListener("touchmove", function(evt){evt.preventDefault()},{ passive: false });
    this.$nextTick(()=>{
      let wrapper = document.querySelector('.wrapper') 
      let scroll = new Bscroll(wrapper, {})
      setInterval(() => {
          scroll.refresh();
          console.log("scroll刷新")
      }, 2000);
    })
  }
};
</script>
