<template>
  <div class="commonPhone">
    <page-header @btn-action="openApp" />
    <div class="commonPhoneContent">
      <div class="phoneList" v-if="phoneInfo != null">
        <a :href="'tel:'+phoneInfo.phone">
          <div class="phoneCard clearfix">
            <img :src="phoneInfo.picUrl" class="imgPicUrl" />
            <div class="hotPhone">
              <div class="descStyle">{{phoneInfo.remark}}</div>
              <div class="telStyle">{{phoneInfo.phone}}</div>
            </div>
            <van-image class="commonPhoneStyle" :src="phoneImg" />
            <div class="contactStyle">联系TA</div>
          </div>
        </a>
      </div>
      <div class="bottomBtn" @click="openApp">打开APP</div>
    </div>
  </div>
</template>

<script>
import { getCommonPhoneInfo } from "../../service/shareService";
import PageHeader from "../../components/WechatShare/PageHeader";
import { Toast } from "vant";

export default {
  name: "commonPhone",
  mounted() {
    this.getPhoneList();
  },
  data() {
    return {
      phoneInfor: [],
      commonPhoneDefaule: false,
      title: "常用电话",
      phoneImg: require("../../assets/common/mall_icon_call.png"),
      phoneId: "",
      phoneInfo: null
    };
  },
  components: {
    PageHeader
  },
  mounted() {
    this.phoneId = this.$route.params.id.split("=")[1];
    this.getPhoneList();
  },
  methods: {
    getPhoneList() {
      getCommonPhoneInfo({ id: this.phoneId })
        .then(res => {
          if (res.resultCode == 0) {
            this.phoneInfo = res.data.shareData;
          } else {
            Toast(res.msg);
          }
        })
        .catch(error => {
          Toast("网络异常，请稍后重试！");
        });
    },
    openApp() {
      let urlString = "myfamily?type=4";
      this.shareOpenApp(urlString);
    }
  }
};
</script>

