<template>
  <div class="activityTemplate">
    <page-header @btn-action="openApp" />
    <!-- 商品列表 -->
    <div class="wrapper">
      <div class="goodsList">
        <div
          class="goodsItem"
          v-for="(goodsItem,goodsIndex) in goodsListData"
          :key="'goodsItem'+goodsIndex"
          @click="openApp()"
        >
          <div class="goodsImg">
            <img :src="goodsItem.spuPicUrl" alt />
            <div class="outSellBox" v-show="goodsItem.storeNumber==0">补货中</div>
            <div
              class="jinshengNum"
              v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0"
            >仅剩{{goodsItem.storeNumber}}件</div>
            <div
              class="fullIcon"
              :style="{backgroundColor:goodsItem.labelColor}"
              v-if="goodsItem.listLabel"
            >{{goodsItem.listLabel}}</div>
          </div>
          <div class="goodsInfo">
            <div class="goodsName">{{goodsItem.spuName}}</div>
            <div class="goodsDesc">{{goodsItem.spuSalePoint}}</div>
            <div class="goodsSale">月售{{goodsItem.virtualSale | threshold}}</div>
            <div class="goodsPriceBx">
              <div class="goodsPriceInfo">
                <div class="goodsPrice">
                  <div class="goodsPriceIcon">￥</div>
                  <div class="goodsPriceNum">{{goodsItem.skuPrice|priceFilters}}</div>
                </div>
                <div
                  class="goodsLinePrice"
                  v-if="goodsItem.priceLine"
                >￥{{goodsItem.priceLine|priceFilters}}</div>
              </div>
              <div class="addMinusBtn">
                <div class="clickBox" @click.stop="openApp(goodsItem)"></div>
                <div class="cartNumBox" v-show="goodsItem.cartCount!=0">
                  <div class="cartNum">{{goodsItem.cartCount}}</div>
                </div>
                <img class="addBtn" ref="addBtn" :src="img.addToCart" alt />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="emptyView" v-if="showFinishView">
        <!-- 活动结束后的缺省页 -->
        <div class="empDataContent">
          <img class="emptyImg" src="../../assets/images/no_shoucang_img.png" />
          <p style="margin-top: 1rem;font-size: 16px;color:#333333;">活动已结束，暂无活动</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getShareActivityDetail,
  getOfflineActivityGoods,
} from "../../service/shareService";
import { Toast } from "vant";
import PageHeader from "../../components/WechatShare/PageHeader";
import Bscroll from "@better-scroll/core";

export default {
  components: {
    PageHeader,
  },
  data() {
    return {
      goodsListData: [], //商品集合
      sourceList: {}, //模板信息
      shareId: 0,
      activityId: "",
      showMore: false, //底部显示更多
      img: {
        shoppingCart: require("@/assets/img/superiorProducts/navbar_icon_shoopingCart_black.png"),
        share: require("@/assets/img/superiorProducts/navbar_icon_share_black.png"),
        xwjlogo: require("../../assets/share/xinwojiaLogo36@2x.png"),
        addToCart: require("../../assets/shopCart/mall_icon_add.png"),
      },
      showFinishView: false,
    };
  },
  filters: {
    threshold(data) {
      return data >= 10000
        ? data / 10000 >= 10
          ? "9万+"
          : Math.floor(data / 10000) + "万+"
        : data;
    },
    priceFilters(data) {
      return data >= 10000
        ? Math.floor((data / 10000) * 100) / 100 + "万"
        : data;
    },
  },
  methods: {
    goToBack() {
      this.$router.go(-1); //返回上一层
    },
    //获取商品活动页商品信息
    getActivityLists() {
      this.activityId = this.$route.params.id.split("=")[1];
      const data = {
        id: this.activityId,
      };
      console.log("活动模板");
      console.log("data:" + data);
      getOfflineActivityGoods(data).then((res) => {
        if (res.resultCode == 0) {
          this.goodsListData = res.data;
        } else {
          this.showFinishView = true;
        }
      });
      this.getActivityTepmlate();
    },
    //获取活动模板信息
    getActivityTepmlate() {
      const data = {
        id: this.activityId,
      };
      getShareActivityDetail(data).then((res) => {
        if (res.resultCode == 0) {
          this.sourceList = res.data;
          this.showFinishView = res.data ? res.data.dataStatus != "1" : true;
          if (this.sourceList) {
            document.title = this.sourceList.title;
          }
        } else {
        }
      });
    },
    openApp(goodsItem) {
      let urlString =
        "myfamily?templateId=" + this.activityId + "&offlineFlag=1";
      if (!this.activityId) {
        urlString = "myfamily";
      } else {
        urlString =
          "myfamily?type=8&offlineFlag=1&templateId=" +
          this.activityId +
          (goodsItem ? "&spuCode=" + goodsItem.spuCode : "");
      }
      this.shareOpenApp(urlString);
    },
    handleScroll: function () {
      const _this = this;
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      const h =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight; //可见宽度
      if (scrollTop >= document.documentElement.scrollHeight - h) {
        _this.showMore = true;
      } else {
        _this.showMore = false;
      }
    },
  },
  mounted: function () {
    this.getActivityLists();
    // window.addEventListener("scroll", this.handleScroll, true);
    document.addEventListener(
      "touchmove",
      function (evt) {
        evt.preventDefault();
      },
      { passive: false }
    );
    this.$nextTick(() => {
      let wrapper = document.querySelector(".wrapper");
      let scroll = new Bscroll(wrapper, { click: true });
      setInterval(() => {
        scroll.refresh();
        console.log("scroll刷新");
      }, 2000);
    });
  },
};
</script>

