        <div class="conWrap">
          <!-- 投诉报修提交 -->
          <div class="conBoxTop"></div>

          <!-- 标签选择 -->
          <section class="labelBox">
            <div class="labelHeader">{{labelHeader}}</div>
            <div class="labelList">
              <div
                :class="{labelItem:true,labelChoose:activeLabel == index}"
                v-for="(item,index) in labelList"
                :key="index"
                @click="chooseLabel(index)"
              >{{item.labelName}}</div>
            </div>
            <div class="space"></div>
          </section>

          <!-- 问题描述 -->
          <section class="questionBox">
            <div class="question">
              <div class="questionHeader">{{questionHeader}}</div>
              <div class="questionCon">
                <textarea
                  ref="textArea"
                  v-model="content"
                  class="questionInfo"
                  cols="30"
                  rows="5"
                  maxlength="1000"
                  readonly
                  placeholder="请输入详细问题，我们一定努力解决"
                ></textarea>
              </div>
            </div>
          </section>
          <section class="uploadImgBox">
            <div class="uploadImgList">
              <div class="uploadImgItem">
                <img class="uplaodImg" :src="uploadImg" />
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
    <div class="bottomBtn" @click="openApp">打开信我家APP进行提交</div>
  </div>
</template>

<script>
import { getRepairDetail } from "../../service/shareService";
import PageHeader from "../../components/WechatShare/PageHeader";
import { Toast } from "vant";

export default {
  name: "complaintReportingAdd",
  components: {
    PageHeader,
  },
  data() {
    return {
      content: "",
      title: "报事报修",
      typeFlag: 0,
      labelHeader: "请选择问题标签",
      questionHeader: "问题描述",
      pullDownIcon: require("@/assets/images/whitedown.png"),
      chooseAreaTitle: "请选择房间",
      headerSTyle: {
        background: "rgba(0,0,0,0)",
        color: "#F9F9F9",
        boxShadow: "none",
      },
      labelList: [],
      activeLabel: 0,
      uploadImg: require("@/assets/images/upload_img.png"),
      uploadImgList: [],
      imgIndex: 0,
      uploadImgLists: [],
      uploadImgSuc: [],
      isDisplayArrow: true, //是否显示小区选择箭头，新建工单进入显示，修改工单进入隐藏
      repairId: "",
      appCode: null,
    };
  },
  computed: {
    //超过最大数不显示添加
    accountAddImage() {
      return this.uploadImgList < 4;
    },

    //相册选择图片最大数值
    maximumImagesCount() {
      return 4 - this.uploadImgList.length;
    },
  },
  mounted() {
    this.repairId = this.$route.params.id.split("=")[1];
    getRepairDetail({ id: this.repairId })
      .then((res) => {
        if (res.resultCode == 0) {
          this.setDataSource(res.data.shareData.repairFlag);
        } else {
          Toast(res.msg);
        }
      })
      .catch((error) => {
        Toast("网络异常，请稍后重试！");
      });
  },
  methods: {
    setDataSource(type) {
    //   报事报修：0008，投诉建议：0009
      this.appCode = type == 0 ? "0008" : "0009";
      if (type == 0) {
        document.title = "报事报修";
        this.labelList = [{ labelName: "报事报修" }, { labelName: "建议" }];
      } else {
        this.labelList = [{ labelName: "投诉建议" }, { labelName: "建议" }];
        document.title = "投诉建议";
      }
    },
    // 选中标签
    chooseLabel(index) {
      this.activeLabel = index;
    },
    openApp() {
      let urlString = "myfamily?type=6" + (this.appCode ? ("&appCode=" + this.appCode) : "" );
      this.shareOpenApp(urlString);
    },
  },
};
</script>

