<template>
  <div class="pageView">
    <page-header @btn-action="openApp" />
    <div class="mainView" v-if="Object.keys(inviteInfo).length!=0">
      <div class="topContent">
        <span class="invitText">{{inviteInfo.ownerName}}（{{inviteInfo.formatPhone}}）邀请您成为</span>
        <span class="invitRoom">【{{inviteInfo.roomAllName}}】</span>
        <span class="invitText">的一员</span>
      </div>
      <div class="bottomContent">
        <span class="invitText">请验证您的身份：</span>
        <div class="phoneInfo">
          <span class="invitePhone">+86 {{inviteInfo.phone.substring(0, 3)}}</span>
          <diy-input ref="msgInput" :codeLength="4"></diy-input>
          <span class="invitePhone">{{inviteInfo.phone.substr(7, 4)}}</span>
        </div>

        <div class="nextStepBtn" @click="nextStepAction">下一步</div>
      </div>
    </div>
  </div>
</template>

<script>
import DiyInput from "../../common/component/verifyInput";
import PageHeader from "../../components/WechatShare/PageHeader";
import { getInviteJoinInfo } from "../../service/shareService";
import { Toast } from "vant";
export default {
  name: "invitationRoot",
  components: {
    PageHeader,
    DiyInput
  },
  data() {
    return {
      inviteId: "",
      inviteInfo: {}
    };
  },
  mounted() {
    this.inviteId = this.$route.params.id.split("=")[1];
    console.log(this.inviteId);
    this.getInviteInfo();
    this.androidShareOpenApp("myfamily?type=1"); //android 拉起APP
  },
  computed: {
    verifyStr() {
      return this.$refs.msgInput.code;
    }
  },
  methods: {
    getInviteInfo() {
      getInviteJoinInfo({ id: this.inviteId })
        .then(res => {
          if (res.resultCode == 0) {
            this.parasData(res.data);
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(error => {
          this.$toast("网络异常,请稍后再试");
        });
    },
    parasData(data) {
      this.inviteInfo = data.shareData;
      let phone = this.inviteInfo.ownerPhone;
      this.inviteInfo.formatPhone =
        phone.substring(0, 3) + "****" + phone.substr(7, 4);
      this.inviteInfo.ownerCode = data.ownerCode;
    },
    openApp() {
      let urlString = "myfamily?type=1";
      this.shareOpenApp(urlString);
    },
    nextStepAction() {
      console.log(this.inviteInfo.phone.substring(3, 7));
      if (this.verifyStr.length != 4) {
        this.$toast("请补充完整手机号中间四位数字");
        return;
      }
      if (this.verifyStr != this.inviteInfo.phone.substring(3, 7)) {
        this.$toast("身份验证失败!");
        return;
      }
      this.$router.push({
        name: "invitationRegist",
        query: { data: JSON.stringify(this.inviteInfo) }
      });
    }
  }
};
</script>
