          <div class="goods-group" v-if="item.likerList && item.likerList.length > 0">
            <img src="../../assets/find/praise_icon.png" alt />
            <span v-for="(ele,a) in item.likerList" v-bind:key="a">
              {{ele.nickName}}
              <i v-if="a != item.likerList.length-1">,</i>
            </span>
          </div>
          <div v-if="item.commentList.length > 0" class="reply-group-box">
            <div type="flex" class="reply-group" v-for="(eva,d) in item.commentList" v-bind:key="d">
