<script>
import PageHeader from "../../components/WechatShare/PageHeader";

import { getNoteDetail } from "../../service/shareService";
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  components: {
    PageHeader,
  },
  data() {
    return {
      noteId: "",
      noteData: null,
    };
  },
  mounted() {
    this.noteId = this.$route.params.id.split("=")[1];
    this.initData();
  },
  methods: {
    initData() {
      getNoteDetail({ id: this.noteId })
        .then((res) => {
          if (res.resultCode == 0) {
            this.noteData = [res.data.shareData];
            console.log(this.noteData);
          } else {
            Toast(res.msg);
          }
        })
        .catch((error) => {
          Toast("网络异常，请稍后重试！");
        });
    },
    openApp() {
      let urlString = "myfamily?type=7";
      this.shareOpenApp(urlString);
    },
    preview(imgArr, index) {
      let resortArr = [];
      let tempImgList = [...imgArr];
      let temp = [];
      for (let i = 0; i < index; i++) {
        temp.push(tempImgList.shift());
      }
      resortArr = tempImgList.concat(temp);

      ImagePreview(resortArr);
    },
  },
};
</script>
