<script>
import {
  getQuestSubjectOption,
  savePaperReader
} from "../../service/shareService";
// import { Toast } from "vant";
import PageHeader from "../../components/WechatShare/PageHeader";

export default {
  name: "sharePaper",
  components: {
    PageHeader
  },
  data() {
    return {
      title: "问卷详情",
      btnName: "下一题",
      btnFlag: 0,
      isDisplayed: 0,
      isNowForbid: true,
      questionType: "单选",
      message: "",
      radio: "",
      nowNum: 1,
      totalNum: 1,
      isShow: true,
      list: {},
      result: [],
      item: {},
      con: {},
      questData: [],
      titleName: "",
      titleNo: "",
      questName: "",
      btnFlag: 0,
      isSaveing: false,
      iconImg: require("@/assets/images/question.png"),
      questionId: ""
    };
  },
  mounted() {
    this.questionId = this.$route.params.id.split("=")[1];
    console.log(this.questionId);
    this.initData();
    this.androidShareOpenApp("myfamily?type=2"); //android 拉起APP
  },
  methods: {
    isRead() {
      let data = {
        PUBLISH_CODE: "2020011910245579413",
        PAPER_CODE: "2020011910061834746",
        USER_CODE: "2020032609200321743",
        USER_NAME: "3495_yz",
        USER_NICK_NAME: "3495_yz",
        ROOM_CODE: "12120101"
      };
      data = { data: JSON.stringify(data) };
      savePaperReader(data)
        .then(res => {})
        .catch(error => {});
    },
    initData() {
      getQuestSubjectOption({ id: this.questionId })
        .then(res => {
          console.log(res);
          if (res.resultCode == 0) {
            let data = res.data.shareData;
            this.isShow = true;
            this.nowNum = data.sortNo;
            this.totalNum = data.maxSortNo;
            this.titleName = data.paperName;
            this.titleNo = data.sortNo;
            this.questName = data.questName;
            this.list.optionList = data.questItemList;
            this.list.QUEST_TYPE_FLAG = data.questTypeFlag;
            this.list.SELECT_TYPE_FLAG = data.selectTypeFlag;
            this.isTypeQus();
          } else {
          }
        })
        .catch(error => {});
    },
    // 题型判断
    isTypeQus() {
      if (this.list.QUEST_TYPE_FLAG == 1 && this.list.SELECT_TYPE_FLAG == 1) {
        this.questionType = "单选";
      } else if (
        this.list.QUEST_TYPE_FLAG == 1 &&
        this.list.SELECT_TYPE_FLAG == 0
      ) {
        this.questionType = "多选";
      } else if (this.list.QUEST_TYPE_FLAG == 0) {
        this.questionType = "问答";
      }
    },
    openApp() {
      let urlString = "myfamily?type=2";
      this.shareOpenApp(urlString);
    }
  }
};
</script>

