import axios from "axios";
import { Toast } from "vant";
import qs from "qs";
import config from "./common/config";

let isFormHeader = false;
let http = axios.create({
    baseURL: config.baseUrl,
    withCredentials: false,
    timeout: 30000,
    headers: {
        "Content-Type": "application/json;charset=utf-8"
    },
    transformRequest: [function (data) {
        if (isFormHeader) {
            return qs.stringify(data);
        } else {
            return JSON.stringify(data);
        }
    }]
});
let loading; //定义loading变量
let needLoadingRequestCount = 0;
let accountShowLoaing = true;
function startLoading() {
    loading = Toast.loading({
        duration: 0,
        mask: true,
        message: '加载中...'
    });
}

function endLoading() {
    if (loading) {
      loading.clear();
    }
}

function showFullScreenLoading() {
    if (needLoadingRequestCount === 0 && accountShowLoaing) {
        startLoading();
    }
    needLoadingRequestCount++;
}

function tryHideFullScreenLoading() {
    if (needLoadingRequestCount <= 0) return;
    needLoadingRequestCount--;
    if (needLoadingRequestCount === 0) {
        endLoading();
    }
}
http.interceptors.request.use(
    config => {
        showFullScreenLoading();
        if (isFormHeader) {
            config.headers["Content-Type"] = "application/x-www-form-urlencoded;charset=utf-8";
        }
        return config;
    },
    error => {
        console.log(error);
    }
);

http.interceptors.response.use(
    response => {
        tryHideFullScreenLoading();
        accountShowLoaing = true;
        return response;
    },
    error => {
        accountShowLoaing = true;
        // // 错误的请求结果处理，这里的代码根据后台的状态码来决定错误的输出信息
        // if (error && error.response) {
        //     error.message = "网络异常，请稍后重试！";
        //     //  Toast("网络异常，请稍后重试！"); 
        // }
        return Promise.reject(error);
    }
);

function apiAxios(method, url, params, serviceType = "baseUrl") {
    // serviceType = serviceType === null ? baseUrl : serviceType;
    if (serviceType === null) {
        serviceType = "baseUrl";
    }
    return new Promise((resolve, reject) => {
        let reqUrl;
        if (/http|https/.test(url)) {
            reqUrl = url;
        } else {
            let { baseUrl, homeUrl } = config;
            reqUrl = serviceType === 'baseUrl' ? baseUrl + url : homeUrl + url;
        }
        http({
            method: method,
            url: reqUrl,
            data: method === "POST" || method === "PUT" ? params : null,
            params: method === "GET" || method === "DELETE" ? params : null
        })
            .then(function (res) {
                tryHideFullScreenLoading();
                if (res.status === 200) { //http 响应状态是200为正常         //  && res.data.status 这个不一定有
                    resolve(res.data);
                } else if (res.status === 200 && !res.data.status) {
                    resolve(res.data);
                } else {
                    reject(res.message);
                }
            })
            .catch(function (err) {
                tryHideFullScreenLoading();
                // 服务连接失败err.message为"Network Error", 连接超时（就是超过设定的timeout的时间）err.message为timeout of 3000ms exceeded
                reject(err.message || err)
            });
    });
}

export default {
    get: function (url, params, servceType, accountLoaing = true) {
        isFormHeader = true;
        accountShowLoaing = accountLoaing;
        return apiAxios("GET", url, params, servceType);
    },
    post: function (url, params, isFormData, servceType, accountLoaing = true) {
        isFormHeader = isFormData;
        accountShowLoaing = accountLoaing;
        return apiAxios("POST", url, params, servceType);
    },
    put: function (url, params, servceType) {
        return apiAxios("PUT", url, params, servceType);
    },
    delete: function (url, params, servceType) {
        return apiAxios("DELETE", url, params, servceType);
    }
};
