//获取协议内容服务
const getProtocolService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/app/owner/login/getProtocol.do', { data: JSON.stringify(reqData) }, true, 'homeUrl').then(noteData => {
            resolve(noteData.data);
        }).catch(error => {
            reject(error);
        })
    });
}

