<template>
  <div class="diyInput">
    <div class="v-code">
      <input
        ref="vcode"
        id="vcode"
        type="tel"
        autocomplete="off"
        :maxlength="codeLength"
        v-model="code"
        @focus="focused = true"
        @blur="focused = false"
        :disabled="telDisabled"
      />

      <label
        for="vcode"
        class="line"
        :style="{'width':codeLength<=4 ? '25px' : '15px'}"
        v-for="(item,index) in codeLength"
        :key="index"
        :class="{'animated': focused && cursorIndex === index}"
        v-text="codeArr[index]"
      ></label>
    </div>
  </div>
</template>

<script>
export default {
  name: "diyInput",
  props: {
    codeLength: {
      default: 6,
      type: Number
    } // 验证码长度
  },
  data() {
    return {
      code: "", // input value
      telDisabled: false, // 判断是否输入完
      focused: false // 让input焦点, 决定光标显示所在的位置
    };
  },
  computed: {
    codeArr() {
      // 将 input value 分隔数组 [1, 2, 3]
      return this.code.split("");
    },
    cursorIndex() {
      // 判断code输入长度，光标显示在对应label上
      return this.code.length;
    }
  },
  watch: {
    code(newVal) {
      // 限制 非数字
      this.code = newVal.replace(/[^\d]/g, "");
      if (newVal.length > this.codeLength - 1) {
        this.$refs.vcode.blur();
      }
    }
  },
  mounted() {}
};
</script>

