  position: relative;
  width: 15px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  font-size: 18px;
  margin-right: 10px;
}
.v-code .line::after {
  display: block;
  position: absolute;
  content: "";
  left: 0;
  width: 100%;
  bottom: 0;
  height: 2px;
  background-color: #333333;
  /* transform: scaleY(0.5); */
  transform-origin: 0 100%;
}
.v-code .line.animated::before {
  display: block;
  position: absolute;
  left: 50%;
  top: 20%;
  width: 1px;
  height: 60%;
  content: "";
  background-color: #333333;
  animation-name: coruscate;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}

