import http from "../http";
import { Toast } from "vant";


export {
    getDetailService,
    verifyPhone,
    approvalIdenti,
    getMsgCode,
    getIdentityTagList,
    getRsaPublicKey
}

//获取用户邀请列表
const getDetailService = (params) => {
    console.log(params)
    return new Promise((resolve, reject) => {
        http.get('xwj-property-house/house/binding/getMemberBindingDetail', params).then(result => {
            resolve(result);
        }).catch(error => {
            Toast('网络异常，请稍后重试！')
            reject(error);
        })
    });
}

//验证手机号
const verifyPhone = (reqParam) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-property-house/house/binding/checkPropertyOwnerPhone', reqParam, false).then(result => {
            if (result.resultCode == '0') {
                resolve(result.data)
            }else{
                Toast(result.msg)
                reject(result.msg)
            }
            // result.resultCode == '0' ? resolve(result.data) : reject(result.msg);
        }).catch(error => {
            Toast('网络异常，请稍后重试！')
            reject(error);
        })

    });
}

